<template>
    <div id="segment">
        <div style="overflow-x: scroll" id="pattern_preview">
            <PatternView :pattern_memory="pattern_memory"></PatternView>
        </div>
        <Card class="lower" id="header">
            <div slot="header" class="clearfix">
                <span>Header</span>
            </div>
            <HeaderSeg :header="header"></HeaderSeg>
        </Card>
        <Card class="lower" id="control_card">
            <div slot="header" class="clearfix">
                <span>Control Card</span>
            </div>
            <ControlCardSeg :control_card="control_card"></ControlCardSeg>
        </Card>
        <Card class="lower" id="pattern">
            <div slot="header" class="clearfix">
                <span>Pattern Memory</span>
            </div>
            <PatternSeg :pattern_memory="pattern_memory"></PatternSeg>
        </Card>
        <Card class="lower" id="default">
            <div slot="header" class="clearfix">
                <span>Default Segment</span>
            </div>
            <DefaultSeg :default_seg="default_seg"></DefaultSeg>
        </Card>
    </div>
</template>

<script>
    import {SegmentDict, ControlCardDict} from '../utils/Dict'
    import PatternSeg from './PatternSeg'
    import PatternView from './PatternView.vue'
    import ControlCardSeg from './ControlCardSeg'
    import HeaderSeg from './HeaderSeg'
    import DefaultSeg from './DefaultSeg'
    import config from '../utils/Config'
    import {  Card } from 'element-ui';

    export default {
        name: "Segment",
        components:{
            PatternView,
            PatternSeg,
            ControlCardSeg,
            HeaderSeg,
            DefaultSeg,
            Card
        },
        created(){
            this.parseTripleZero()
        },
        watch:{
            text(val){
                // console.log(val);
                // this.text = val
                // this.parseTripleZero()
                this.parseTripleZero()
            }
        },
        props:["text"],
        data(){
            return{
                header:{},
                pattern_memory:{},
                control_card:{},
                default_seg:{}
            }
        },

        methods:{
            headerParse(){
                let SEGMENT_CODE = config.header.SEGMENT_CODE;
                // let NEXT_SEGMENT_CODE = config.header.NEXT_SEGMENT_CODE;
                // let reg = config.header.reg;
                let length = config.header.LENGTH;

                // let str = this.text.substring(0, this.text.lastIndexOf(NEXT_SEGMENT_CODE));
                let str = this.text.substring(0, length);

                let params = this.text.split("0d");

                let file_name_ascii = params[1].substring(4, params[1].lastIndexOf("09"));
                let file_name_str = "";
                for (let k = 0; k < file_name_ascii.length; k += 2) {
                    file_name_str += String.fromCharCode(parseInt(file_name_ascii.substring(k, k+2), 16));
                }
                this.header.filename = file_name_str;

                for (let i = 2; i < 7; ++i) {
                    let param_name = String.fromCharCode(parseInt(params[i].substring(0, 2), 16));
                    let param_val = String.fromCharCode(parseInt(params[i].substring(params[i].indexOf("3d")+2, params[i].lastIndexOf("09")), 16));

                    this.header[param_name] = param_val;
                }

                let data = "";
                for (let i = 0; i < str.length; i += 2) {
                    let char = str.substring(i, i+2);
                    data = data + char + " ";
                    if (char == "0d"){
                        data = data + "<br/>";
                    }
                }
                this.header.data = data;
            },
            controlCardParse(){
                let SEGMENT_CODE = config.controlcard.SEGMENT_CODE;
                // let NEXT_SEGMENT_CODE = config.controlcard.NEXT_SEGMENT_CODE;
                let HEADER_LENGTH = config.controlcard.HEADER_LENGTH;
                let str = this.text;
                let reg = config.controlcard.reg;
                let seg_ending = "03030100";
                for (let i = 0; i < config.controlcard.SEGMENT_ENDING_LENGTH; i++) {
                    seg_ending += "0";
                }
                seg_ending += "0080";
                // console.log(seg_ending);
                let split_segments = () => {
                    // let starting = "0002";
                    // let ending = "8303030100";
                    // let segs = this.str.match(this.reg)[0].split(ending);
                    if (str.match(reg) == null) return;
                    let seg = str.match(reg)[0].split("0002c60303c0");
                    seg.shift();
                    for (let i = 0; i < seg.length; ++i) {
                        // segs[i] = segs[i].substring(segs[i].indexOf(starting))
                        seg[i] = SEGMENT_CODE + seg[i];
                    }
                    let last_seg = seg[seg.length-1];
                    // console.log(last_seg);
                    seg[seg.length-1] = last_seg.substring(0, last_seg.indexOf(seg_ending));
                    seg[seg.length-1] = seg[seg.length-1] + seg_ending;
                    // console.log(seg[seg.length-1])
                    return seg;
                }

                let parse_seg_header =(str)=> {
                    let res = "";
                    let card_id = "";
                    for (let k = 0; k < str.length; ++k) {
                        if (k == 12 || k == 13) {
                            card_id += str[k];
                        }

                        if (k != 0 && k % 4 == 0) {
                            res += " ";
                        }
                        res += str[k];
                    }
                    res += "<br/><br/>";
                    if (card_id == "00") {
                        card_id = "Main";
                    } else {
                        card_id = "Sub" + card_id;
                    }
                    this.control_card[card_id] = {"header": res, "body": "", "tail": "", "blocks": ""};

                    return card_id;
                }

                let extract_card_info = (str)=> {
                    let card_info = {};

                    let binary = '';
                    for (let i = 0; i < str.length; ++i) {
                        let s = parseInt(str[i], 16).toString(2);
                        for (let k = 0; k < 4 - s.length; ++k) {
                            binary += '0';
                        }
                        binary += s;
                    }

                    // Carrier Evacuation
                    // pos8 == 1 -> left
                    // pos9 == 1 -> right
                    // pos8&9 == 1 -> left & right
                    if (binary[8] == '1' && binary[9] == '1') {
                        card_info['Carrier Evacuation'] = 'left & right';
                    } else if (binary[8] == '1') {
                        card_info['Carrier Evacuation'] = 'left';
                    } else if (binary[9] == '1') {
                        card_info['Carrier Evacuation'] = 'right';
                    } else {
                        card_info['Carrier Evacuation'] = 'None';
                    }

                    // Carrier Original Position
                    // pos10 == 1 -> checked
                    if (binary[10] == '1') {
                        card_info['Carrier Original Position'] = '√';
                    } else {
                        card_info['Carrier Original Position'] = '×';
                    }

                    // Count up
                    // pos11 == 1 -> checked
                    if (binary[11] == '1') {
                        card_info['Count Up'] = '√';
                    } else {
                        card_info['Count Up'] = '×';
                    }


                    // Op. Stop
                    // pos12 == 1 -> Op. Stop
                    // pos136 == 1 -> Pause 2
                    // pos137 == 1 -> Pause 1
                    // pos137&138 == 1 -> Pause 3
                    if (binary[12] == '1') {
                        card_info['Op. Stop'] = 'Op. Stop';
                    } else if (binary[137] == '1' && binary[138] == '1') {
                        card_info['Op. Stop'] = 'Pause 3';
                    } else if (binary[137] == '1') {
                        card_info['Op. Stop'] = 'Pause 2';
                    } else if (binary[138] == '1') {
                        card_info['Op. Stop'] = 'Pause 1';
                    } else {
                        card_info['Op. Stop'] = 'None';
                    }

                    // Abc Adjust
                    // pos13 == 1 -> checked
                    if (binary[13] == '1') {
                        card_info['abc Adjust'] = '√';
                    } else {
                        card_info['abc Adjust'] = '×';
                    }

                    // Sync. Stitch Adj.
                    // pos14 == 1 -> checked
                    if (binary[14] == '1') {
                        card_info['Sync. Stitch Adj.'] = '√';
                    } else {
                        card_info['Sync. Stitch Adj.'] = '×';
                    }

                    // DSCS
                    // pos17 == 1 -> Ignore
                    // pos18 == 1 -> Start/End
                    if (binary[17] == '1') {
                        card_info['DSCS'] = 'Ignore';
                    } else if (binary[18] == '1') {
                        card_info['DSCS'] = 'Start/End';
                    } else {
                        card_info['DSCS'] = 'None';
                    }

                    // Yarn Address
                    // pos24-31
                    let hexYarnAdd = parseInt(binary.substring(24, 32), 2).toString(16);
                    let deYarnAdd = parseInt(hexYarnAdd, 16).toString();
                    card_info['Yarn Address'] = hexYarnAdd + " (" + deYarnAdd + ")";

                    // Yarn Out
                    // pos32 == 1 -> checked
                    if (binary[32] == '1') {
                        card_info['Yarn Out'] = '√';
                    } else {
                        card_info['Yarn Out'] = '×';
                    }

                    // Yarn In
                    // pos33 == 1 -> checked
                    if (binary[33] == '1') {
                        card_info['Yarn In'] = '√';
                    } else {
                        card_info['Yarn In'] = '×';
                    }


                    // Final Stitch
                    // pos35 == 1 -> 64
                    // pos40-47
                    let deFinalStitch = parseInt(binary.substring(40, 48), 2);
                    if (binary[35] == '1') {
                        deFinalStitch += 64;
                    }
                    card_info['Final Stitch'] = '' + deFinalStitch;

                    // Stroke Adjust
                    // Value: 1 2 3

                    let strokeAdjust = parseInt(binary.substring(36, 38), 2);
                    card_info['Stroke Adjust'] = strokeAdjust;


                    // Yarn Off
                    // checkbox
                    if(binary[39] == '1'){
                        card_info['Yarn Off'] = '√';
                    }
                    else{
                        card_info['Yarn Off'] = '×';
                    }


                    // Sub Roller
                    let subRoller = parseInt(binary.substring(40,42), 2);
                    if(subRoller == 1){
                        card_info['Sub Roller'] = "On";
                    }
                    else if(subRoller == 2){
                        card_info['Sub Roller'] = "Off";
                    }

                    // Stitch/Loop Len.
                    // pos52-59
                    let hexStitch = parseInt(binary.substring(48, 56), 2).toString(16);
                    let deStitch = parseInt(hexStitch, 16).toString();
                    card_info['Stitch/Loop Len.'] = hexStitch + " (" + deStitch + ")";


                    // Racking1*1
                    // Button

                    if(binary[55] == '1'){
                        card_info['Racking 1*1'] = '√';
                    }

                    // Racking1/4
                    // Button

                    if(binary[56] == '1') {
                        card_info['Racking 1/4'] = '√';
                    }

                    // Racking 1/2
                    // Button

                    if(binary[57] == '1'){
                        card_info['Racking 1/2'] = '√';
                    }

                    // Front temp+1
                    // pos209 == 1 -> temp+1
                    if (binary[209] == '1') {
                        card_info['Front temp+1'] = '√';
                    } else {
                        card_info['Front temp+1'] = '×';
                    }


                    // Racking
                    // number 0 - 31
                    let topRacking = binary[63];
                    let secondRacking = binary[58];
                    let racking = parseInt(binary.substring(59,62), 2) / 2;
                    if(secondRacking == '1'){
                        racking += 8;
                    }

                    if(topRacking == '1'){
                        racking += 16;
                    }
                    card_info['Racking'] = racking;



                    // Racking L
                    // Button
                    if(binary[62] == '1'){
                        card_info['Racking L'] = '√';
                    }
                    else{
                        card_info['Racking L'] = '×';
                    }

                    // Splicer Inhibit Calc.
                    // value: 1-3
                    let splicerInhibit = parseInt(binary.substring(65,67), 2);
                    card_info['Splicer Inhibit Calc.'] = splicerInhibit;



                    // PAT DEV(F)
                    // Value: 1-15
                    let patDevF = parseInt(binary.substring(67, 71),2);
                    card_info['PAT DEV. (F)'] = patDevF;


                    // PAT DEV(B)
                    // Value: 1-15
                    let patDevB = parseInt(binary.substring(119, 123),2);
                    card_info['PAT DEV. (B)'] = patDevB;


                    // IGN. Block
                    // CheckBox
                    if(binary[71] == '1'){
                        card_info['IGN. Block'] = '√';
                    }
                    else{
                        card_info['IGN. Block'] = '×';
                    }

                    // Jump Course
                    // value:
                    let jumpCourseString = binary.substring(72, 74) + binary.substring(99, 103) + binary.substring(87, 95);
                    let jumpCourse = parseInt(jumpCourseString, 2);
                    card_info['Jump Course'] = jumpCourse;



                    // Sub
                    // value: 1-31
                    let sub = parseInt(binary.substring(74, 79), 2);
                    card_info['Sub'] = sub;


                    // Yarn Remain
                    // Checkbox
                    if(binary[81] == '1'){
                        card_info['Yarn Remain'] = '√';
                    }
                    else if(binary[81] == '0'){
                        card_info['Yarn Remain'] = '×';
                    }


                    // Jump Economy
                    let jumpEconomy = parseInt(binary.substring(82, 87), 2);
                    card_info['Jump Economy'] = jumpEconomy;


                    // ABS. Jump
                    // Checkbox
                    if(binary[95] == '1'){
                        card_info['ABS. Jump'] = '√';
                    }
                    else if(binary[95] == '0'){
                        card_info['ABS. Jump'] = '×';
                    }


                    // Fabric Presser B
                    // In & Out
                    let fabricPresser = "";
                    if(binary[108] == '1'){
                        fabricPresser += " In";
                    }

                    if(binary[109] == '1'){
                        fabricPresser += " Out";
                    }
                    card_info['Fabric Presser B (1)'] = fabricPresser.trim();


                    // Fabric Presser B
                    // Checkbox
                    if(binary[118] == '1'){
                        card_info['Fabric Presser B (2)'] = '√';
                    }
                    else if(binary[118] == '0'){
                        card_info['Fabric Presser B (2)'] = '×';
                    }


                    // Fabric Presser F
                    // CheckBox
                    if(binary[117] == '1'){
                        card_info['Fabric Presser F'] = '√';
                    }
                    else if(binary[117] == '0'){
                        card_info['Fabric Presser F'] = '×';
                    }


                    // Dropping Failure
                    // CheckBox
                    if(binary[111] == '1'){
                        card_info['Dropping Failure'] = '√';
                    }
                    else if(binary[111] == '0'){
                        card_info['Dropping Failure'] = '×';
                    }



                    // Sinker Reset
                    // CheckBox
                    if(binary[116] == '1'){
                        card_info['Sinker Reset'] = '√';
                    }
                    else if(binary[116] == '0'){
                        card_info['Sinker Reset'] = '×';
                    }


                    // Final Stitch Course
                    // value: 0-4096
                    card_info['Final Stitch Course'] = parseInt(binary.substring(127, 135), 2);


                    // Op. Stop
                    let opStop = binary.substring(135, 137);
                    if(opStop == "01"){
                        card_info['Op. Stop'] = "Pause 1";
                    }
                    else if(opStop == "10"){
                        card_info['Op. Stop'] = "Pause 2";
                    }
                    else{
                        card_info['Op. Stop'] = "Pause 3";
                    }


                    // Yarn Hold No.
                    let yarnHoldNo = "";
                    if(binary[207] == '1') {
                        yarnHoldNo += " 1";
                    }

                    if(binary[206] == '1'){
                        yarnHoldNo += " 2";
                    }

                    if(binary[205] == '1'){
                        yarnHoldNo += " 3";
                    }

                    if(binary[204] == '1'){
                        yarnHoldNo += " 4";
                    }

                    if(binary[197] == '1'){
                        yarnHoldNo += " 5";
                    }

                    if(binary[189] == '1'){
                        yarnHoldNo += " 6";
                    }

                    if(binary[274] == '1'){
                        yarnHoldNo += " 7";
                    }

                    if(binary[275] == '1'){
                        yarnHoldNo += " 8";
                    }

                    if(binary[150] == '1'){
                        yarnHoldNo += " 9";
                    }

                    if(binary[149] == '1'){
                        yarnHoldNo += " 10";
                    }
                    card_info['Yarn Hold No.'] = yarnHoldNo.trim();


                    // RL Simultaneous Adj. Off
                    // Checkbox
                    if(binary[151] == '1'){
                        card_info['RL Simultaneous Adj. Off'] = '√';
                    }
                    else if(binary[151] == '0'){
                        card_info['RL Simultaneous Adj. Off'] = '×';
                    }


                    // Final Stitch Crs. Unit
                    card_info['Final Stitch Crs. Unit'] = parseInt(binary.substring(156, 159), 2);


                    // Mask Fab. Pile Up Err
                    // Checkbox
                    if(binary[159] == '1'){
                        card_info['Mask Fab. Pile Up Err'] = '√';
                    }
                    else if(binary[159] == '0'){
                        card_info['Mask Fab. Pile Up Err'] = '×';
                    }


                    //Elastic Yarn Advance
                    card_info['Elastic Yarn Advance'] = parseInt(binary.substring(169, 174), 2);

                    // Elastic Yarn Pause
                    let deElasticYarnPause = binary[174];
                    let elasticYarnPause = parseInt(binary.substring(163, 167), 2);
                    if(deElasticYarnPause == '1'){
                        elasticYarnPause += 16;
                    }
                    card_info['Elastic Yarn Pause'] = elasticYarnPause;



                    // Y. Hold Hook Cmd
                    let yHoldHookCmd = config.controlcard.yHoldHookCommand;
                    card_info['Y.Hold Hook Cmd'] = yHoldHookCmd[parseInt(binary.substring(186, 188), 2).toString(10)];


                    // Pattern Position F
                    //



                    // Cleaner
                    // value: On & Off
                    let cleaner = config.controlcard.cleanerValue;
                    card_info['Cleaner'] = cleaner[parseInt(binary.substring(190,192), 2).toString(10)];


                    // Yarn Vacuum
                    // CheckBox
                    if(binary[193] == '1'){
                        card_info['Yarn Vacuum'] = '√';
                    }
                    else if(binary[193] == '0'){
                        card_info['Yarn Vacuum'] = '×';
                    }


                    // Carriage Air
                    // On & Off
                    let carriageAir = config.controlcard.carriageAirValue;
                    card_info['Carriage Air'] = carriageAir[parseInt(binary.substring(198, 200), 2).toString(10)];



                    // Yarn Hold Carrier No.
                    // Value: 1-15
                    card_info['Yarn Hold Carrier No.'] = parseInt(binary.substring(200, 204), 2);


                    // Yarn Inserting Hook
                    // Value: 1-192
                    card_info['Yarn Inserting Hook'] = parseInt(binary.substring(256, 264), 2);


                    // i-DSCS Inlay
                    // Value: 1-31
                    card_info['i-DSCS Inlay'] = parseInt(binary.substring(267, 272), 2);

                    // i-DSCS Pause
                    // Value: 1-31
                    card_info['i-DSCS Pause'] = parseInt(binary.substring(275, 280), 2);


                    // Racking Adjust
                    card_info['Racking Adjust'] = parseInt(binary.substring(314, 316), 2);

                    // Carrier Preparation
                    // Value:
                    let carrierPreparation = binary.substring(300, 304) + binary.substring(288, 296);
                    card_info['Carrier Preparation'] = parseInt(carrierPreparation, 2);


                    // Pattern Position
                    // Value: F / B
                    if(binary[188] == '1'){
                        card_info['Pattern Position'] = "F";
                    }
                    if(binary[197] == '1'){
                        card_info['Pattern Position'] = "B";
                    }



                    let operation = config.controlcard.operation;

                    // Front operation
                    let frontOperation = parseInt(binary.substring(211, 216), 2).toString(16);
                    card_info['Front operation'] = operation[frontOperation];

                    // Front operation parameter 2
                    let hexFrontOperationParameter2 = binary.substring(216, 224).split('');
                    let frontOperationParameter2 = '';
                    for (var i = 0; i < hexFrontOperationParameter2.length; i++){
                        if (hexFrontOperationParameter2[i] == '1') {
                            if(i == 0){
                                frontOperationParameter2 = frontOperationParameter2 + i + ',';
                            }
                            else{
                                frontOperationParameter2 = frontOperationParameter2 + (8 - i) + ',';
                            }
                        }
                    }
                    if (frontOperationParameter2 == ""){
                        frontOperationParameter2 = "None";
                    }
                    else{
                        frontOperationParameter2 = frontOperationParameter2.substring(0, frontOperationParameter2.length - 1)
                    }
                    card_info['Front operation parameter 2'] = frontOperationParameter2;



                    // Front operation parameter 1
                    let hexFrontOperationParameter1 = binary.substring(224, 232).split('');
                    let frontOperationParameter1 = '';
                    for (var i = 0; i < hexFrontOperationParameter1.length; i++){
                        if (hexFrontOperationParameter1[i] == '1') {
                            if(i == 0){
                                frontOperationParameter1 = frontOperationParameter1 + i + ',';
                            }
                            else{
                                frontOperationParameter1 = frontOperationParameter1 + (8 - i) + ',';
                            }
                        }
                    }
                    if (frontOperationParameter1 == ""){
                        frontOperationParameter1 = "None";
                    }
                    else{
                        frontOperationParameter1 = frontOperationParameter1.substring(0, frontOperationParameter1.length - 1)
                    }
                    card_info['Front operation parameter 1'] = frontOperationParameter1;


                    // Back temp+1
                    // pos233 == 1 -> temp+1
                    if (binary[233] == '1') {
                        card_info['Back temp+1'] = 'selected';
                    } else {
                        card_info['Back temp+1'] = '';
                    }

                    // Back operation
                    let backOperation = parseInt(binary.substring(235, 240), 2).toString(16);
                    card_info['Back operation'] = operation[backOperation];


                    // Back operation parameter 2
                    let hexBackOperationParameter2 = binary.substring(240, 248).split('');
                    let backOperationParameter2 = '';
                    for (var i = 0; i < hexBackOperationParameter2.length; i++){
                        if (hexBackOperationParameter2[i] == '1') {
                            if(i == 0){
                                backOperationParameter2 = backOperationParameter2 + i + ',';
                            }
                            else{
                                backOperationParameter2 = backOperationParameter2 + (8 - i) + ',';
                            }
                        }
                    }
                    if (backOperationParameter2 == ""){
                        backOperationParameter2 = "None";
                    }
                    else{
                        backOperationParameter2 = backOperationParameter2.substring(0, backOperationParameter2.length - 1)
                    }
                    card_info['Back operation parameter 2'] = backOperationParameter2;

                    // Back operation parameter 1
                    let hexBackOperationParameter1 = binary.substring(248, 256).split('');
                    let backOperationParameter1 = '';
                    for (var i = 0; i < hexBackOperationParameter1.length; i++){
                        if (hexBackOperationParameter1[i] == '1') {
                            if(i == 0){
                                backOperationParameter1 = backOperationParameter1 + i + ',';
                            }
                            else{
                                backOperationParameter1 = backOperationParameter1 + (8 - i) + ',';
                            }
                        }
                    }
                    if (backOperationParameter1 == ""){
                        backOperationParameter1 = "None";
                    }
                    else{
                        backOperationParameter1 = backOperationParameter1.substring(0, backOperationParameter1.length - 1)
                    }
                    card_info['Back operation parameter 1'] = backOperationParameter1;

                    // Pat. Address (F)
                    // pos304-319
                    // could be wrong
                    let hexFrontPatAddress = parseInt(binary.substring(304, 320), 2).toString(16);
                    card_info['Pat. Address (F)'] = hexFrontPatAddress;

                    // Pat. Address (B)
                    // pos328-343
                    // could be wrong
                    let hexBackPatAddress = parseInt(binary.substring(328, 344), 2).toString(16);
                    card_info['Pat. Address (B)'] = hexBackPatAddress;

                    // Front Operation
                    // pos323 == 1 -> +2
                    // pos325 == 1 -> -1
                    // pos327 == 1 -> +1
                    if (binary[323] == '1') {
                        card_info['Front Operation +1/+2/-1'] = '+2';
                    } else if (binary[325] == '1') {
                        card_info['Front Operation +1/+2/-1'] = '-1';
                    } else if (binary[327] == '1') {
                        card_info['Front Operation +1/+2/-1'] = '+1';
                    } else {
                        card_info['Front Operation +1/+2/-1'] = '';
                    }

                    // Back Operation
                    // pos347 == 1 -> +2
                    // pos349 == 1 -> -1
                    // pos351 == 1 -> +1
                    if (binary[347] == '1') {
                        card_info['Back Operation +1/+2/-1'] = '+2';
                    } else if (binary[349] == '1') {
                        card_info['Back Operation +1/+2/-1'] = '-1';
                    } else if (binary[351] == '1') {
                        card_info['Back Operation +1/+2/-1'] = '+1';
                    } else {
                        card_info['Back Operation +1/+2/-1'] = '';
                    }

                    return card_info;
                }

                let parse_seg_body = (str, id)=> {
                    const line_length = 44;
                    const block_length = 88;
                    let res = "";
                    let blockOrigin = [];
                    for (let k = 0; k < str.length; ++k) {
                        if (k != 0 && k % 2 == 0) {
                            res += " ";
                        }

                        if (k != 0 && k % line_length == 0) {
                            res += "<br/>";
                        }

                        res += str[k];

                        if (k !=0 && (k+1) % block_length == 0) {
                            blockOrigin.push(res)
                            res = ""
                        }
                    }

                    this.control_card[id].body = blockOrigin;

                    let seg_info = new Array();
                    for (let l = 0; l < str.length / block_length; ++l) {
                        let card_info = extract_card_info(str.substring(l * line_length, l * line_length + block_length - 1));
                        seg_info.push(card_info);
                    }

                    this.control_card[id].blocks = seg_info;

                }

                let parse_seg_tail = (str, id) => {
                    const line_length = 64;

                    let res = "<br/>";
                    res = res + str[0] + str[1] + '<br/>';

                    str = str.substring(2);

                    for (let k = 0; k < str.length; ++k) {
                        if (k != 0 && k % 4 == 0) {
                            res += " ";
                        }
                        if (k != 0 && k % line_length == 0) {
                            res += "<br/>";
                        }
                        res += str[k];
                    }

                    this.control_card[id].tail = res;
                }

                let segs = split_segments();
                for (let i = 0; i < segs.length; ++i) {
                    let card_id = parse_seg_header(segs[i].substring(0, HEADER_LENGTH));
                    if (segs[i].lastIndexOf('8303030100') == -1){
                        parse_seg_body(segs[i].substring(HEADER_LENGTH, segs[i].lastIndexOf("03030100")), card_id);
                        parse_seg_tail(segs[i].substring(segs[i].indexOf("03030100")), card_id);
                    }
                    else{
                        parse_seg_body(segs[i].substring(HEADER_LENGTH, segs[i].lastIndexOf("8303030100")), card_id);
                        parse_seg_tail(segs[i].substring(segs[i].indexOf("8303030100")), card_id);
                    }
                }
            },
            patternParse(){
                let SEGMENT_CODE = config.pattern.SEGMENT_CODE;
                // let NEXT_SEGMENT_CODE = config.pattern.NEXT_SEGMENT_CODE;
                let HEADER_LENGTH = config.pattern.HEADER_LENGTH;
                let reg = config.pattern.reg;
                let str = this.text;
                let colorDict = config.pattern.PALETTE;
                let parse_header = (str) => {
                    // console.log("header: " + str);
                    let res = "";

                    for (let k = 0; k < str.length; ++k) {
                        if (k != 0 && k % 2 == 0) {
                            res += " ";
                        }
                        res += str[k];
                    }

                    this.pattern_memory.header = res + "<br/>";
                }

                let parse_body = (str) => {
                    // console.log(str);
                    const line_length = 64;

                    let blocks = str.split("0303");
                    blocks.shift();
                    let res = "";
                    let res_view = []

                    for (let i = 0; i < blocks.length; ++i) {
                        res = res + "0303" + "<br/>";
                        let firstLine = []
                        let secondLine = []
                        for (let k = 0; k < blocks[i].length; ++k) {
                            if (k != 0 && k % 2 == 0) {
                                res += " ";
                            }
                            if (k != 0 && k % line_length == 0) {
                                res += "<br/>";
                            }
                            if(k % 2 == 1) {
                                let hexUnit = blocks[i][k-1] + blocks[i][k];
                                let binaryUnit = ("00000000" + (parseInt(hexUnit, 16)).toString(2)).substr(-8);
                                let firstLineNum = "" + parseInt(binaryUnit.substring(0, 3).split("").reverse().join(""), 2);
                                let secondLineNum = "" + parseInt(binaryUnit.substring(3, 6).split("").reverse().join(""), 2);

                                let firstLineDot = binaryUnit[6] + "0";
                                let secondLineDot = "0" + binaryUnit[7];

                                if(firstLineDot == "10") {
                                    firstLineNum += "."
                                }
                                if(secondLineDot == "01") {
                                    secondLineNum += "."
                                }
                                let firstLineItem = colorDict[firstLineNum];
                                let secondLineItem = colorDict[secondLineNum];

                                firstLine.push(firstLineItem)
                                secondLine.push(secondLineItem)
                            }
                            res += blocks[i][k];
                        }
                        res += "<br/>";
                        res_view.unshift(firstLine);
                        res_view.unshift(secondLine);
                    }
                    this.pattern_memory.body = res;
                    this.pattern_memory.view = res_view;
                }


                let parse_extra = (str) => {
                    const line_length = 36;

                    let res = "";

                    res = res + str.substring(0, 4) + "<br/>" + str.substring(4, 6) + "<br/>";
                    res = res + str.substring(6, 10) + "<br/>" + str.substring(10, 12) + "<br/>";
                    str = str.slice(12);

                    for (let k = 0; k < str.length; ++k) {
                        if (k != 0 && k % 2 == 0) {
                            res += " ";
                        }
                        if (k != 0 && k % line_length == 0) {
                            res += "<br/>";
                        }
                        res += str[k];
                    }

                    this.pattern_memory.extra = res;
                }

                let parse_tail = (str) => {
                    const line_length = 64;

                    let res = "";

                    for (let k = 0; k < str.length; ++k) {
                        if (k != 0 && k % 4 == 0) {
                            res += " ";
                        }
                        if (k != 0 && k % line_length == 0) {
                            res += "<br/>";
                        }
                        res += str[k];
                    }
                    // console.log(res);
                    this.pattern_memory.tail = res;
                }

                let seg_ending = "03030100";
                for (let i = 0; i < config.pattern.SEGMENT_ENDING_LENGTH; ++i) {
                    seg_ending += "0";
                }
                seg_ending += "0080";
                if (str.match(reg) == null) return;
                let seg = str.match(reg)[0];
                parse_tail(seg.substring(seg.indexOf(seg_ending), seg.indexOf(seg_ending)+seg_ending.length));
                seg = seg.substring(0, seg.indexOf(seg_ending));
                // if(seg.indexOf(NEXT_SEGMENT_CODE) > -1){
                //     seg = seg.substring(0, seg.lastIndexOf(NEXT_SEGMENT_CODE));
                // }
                // console.log(seg);
                parse_header(seg.substring(0, HEADER_LENGTH));
                parse_body(seg.substring(HEADER_LENGTH, seg.lastIndexOf("0303")));
                parse_extra(seg.substring(seg.lastIndexOf("0303")));
                // parse_tail(seg.substring(seg.lastIndexOf("03030100")));
            },
            defaultParse(){
                const line_length = 64;
                let str = this.text;
                for (let i = 0; i < SegmentDict.default_segments.length; ++i) {
                    let seg = SegmentDict.default_segments[i];
                    let name = seg.name;
                    if (str.match(seg.reg) == null) continue;
                    let seg_str = str.match(seg.reg)[0];
                    let seg_ending = "";
                    if (name != "00020f040508") {
                        seg_ending = "03030100";
                        for (let k = 0; k < seg.seg_ending_length; ++k) {
                            seg_ending += "0";
                        }
                        seg_ending += "0080";
                        seg_str = seg_str.slice(0, seg_str.indexOf(seg_ending));
                    } else {
                        seg_ending = "ffffffffff";
                        for (let k = 0; k < seg.seg_ending_length; ++k) {
                            seg_ending += "0";
                        }
                        seg_str = seg_str.slice(0, seg_str.indexOf("ffffffffff"));
                    }
 
                    let res = "";

                    for (let k = 0; k < seg_str.length; ++k) {
                        if (k != 0 && k % 4 == 0) {
                            res += " ";
                        }
                        if (k != 0 && k % line_length == 0) {
                            res += "<br/>";
                        }
                        res += seg_str[k];
                    }
                    let formated_ending = "<br/><br/>";
                    for (let k = 0; k < seg_ending.length; ++k) {
                        if (k != 0 && k % 4 == 0) formated_ending += " ";
                        if (k != 0 && k % 64 == 0) formated_ending += "<br/>";
                        formated_ending += seg_ending[k];
                    }
                    res += formated_ending;
                    this.default_seg[name] = res;
                }
            },
            parseTripleZero(){
                this.defaultParse();
                this.headerParse();
                this.controlCardParse();
                this.patternParse();
            }
        }
    }
</script>

